
export default {
	//In obj, replace keys and values equal to oldVal with newValing.
	//Replaces strings in arrays too, and if a value in obj is an object, call recursively.
	obj_val_replace: function(obj, oldVal, newVal){
		if ( (oldVal == newVal) || !obj ){return}

		var key;
		//Iterate over keys 
		for (key in obj){
			if (key == oldVal){
				obj[newVal] = obj[oldVal];
				delete obj[oldVal];
			}
		}

		for (key in obj){

			var val = obj[key];

			if (val == oldVal){obj[key]=newVal}

			if (typeof val == 'object'){

				if (val instanceof Array){
					obj[key] = val.map(x => (x==oldVal ? newVal : x));
				}

				else {
					this.obj_val_replace(val, oldVal, newVal);
				}
			}

		}
	},

	//notes1,notes2 are objs of format {note_id: note}
	mergeNotes: function(notes1,notes2){
		var key;
		var merged_notes = {};
		var temp_id = 0;

		//Replaces ids of new notes with temporary negative numbers so there are no collisions while merging, because ids are positive.
		[notes1,notes2].forEach(function(dict){
			for (key in dict){
				this.obj_val_replace(dict,key,temp_id);	
				temp_id--;
			}
			Object.assign(merged_notes,dict);

		}.bind(this));

		for (key in merged_notes){this.obj_val_replace(merged_notes,key,-key)}
		
		return merged_notes;
	}
}
