//Class for fns that both NoteInstance and NoteContainer share
export default {
	properties : {

	},

	methods : {
		
		NoteInstanceOnly : {

			onDblclick(e){
				this.$emit('dblclick',{instance: this, event: e});
			},

			toggleChildren(){
				this.children_instantiated = true; 
				this.button_text = (this.button_text == '>' ? 'v' : '>');

			},

			
			isVisible(){
				if (this.$parent.$children.length == 0){return false;}
				if (this.$parent == this.root){return true;}
				return (this.$parent.isVisible() && this.$parent.button_text == 'v')
			},

			//No wraparound
			previousSibling(){
					return this.siblings[this.index==0 ? 0 	: this.index-1]
			},


			nextSibling(){
				return this.siblings[this.index+1==this.siblings.length ? this.index : this.index+1]
			},

			shiftUp(){
				if (this.isFirstSibling()){return}
				this.$store.commit("shiftUp",{parent:this.parentNote,child:this.note});

				var ps = this.previousSibling();

				var tmp = this.button_text;
				this.button_text = ps.button_text;
				ps.button_text = tmp;
				
				var tmp = this.children_instantiated;
				this.children_instantiated = ps.children_instantiated;
				ps.children_instantiated = tmp;

				this.$parent.$forceUpdate();
			},

			shiftDown(){
				if (this.isLastSibling()){return}
				this.$store.commit("shiftDown",{parent:this.parentNote,child:this.note});

				var ns = this.nextSibling();

				var tmp = this.button_text;
				this.button_text = ns.button_text;
				ns.button_text = tmp;
				
				var tmp = this.children_instantiated;
				this.children_instantiated = ns.children_instantiated;
				ns.children_instantiated = tmp;

				this.$parent.$forceUpdate();

			},


			resize(){
				if(this.height<this.textArea.scrollHeight){this.height = this.textArea.scrollHeight};
				
				//When deleting a line, scrollHeight is much higher than it should be. This is a workaround.
				if(this.height>this.textArea.scrollHeight){
					this.height = 0;
					this.$nextTick(function(){
						this.height = this.textArea.scrollHeight;
					}.bind(this));
				};
			},

			scrollIntoView(){ 
					var bounding = this.textArea.getBoundingClientRect();
					if (
						bounding.top < 0 ||
						bounding.bottom > window.innerHeight
					) 
					{this.textArea.scrollIntoView();}
			},

			expand(){if(this.isFolded){this.toggleChildren()}},
			collapse(){if (!this.isFolded){this.toggleChildren()}},
			isFirstSibling(){return this.index == 0;},
			isLastSibling(){return this.index == this.siblings.length-1},
			indexAsSibling(){return this.$parent.$children.indexOf(this)},
			wasDeleted(){ return this.indexAsSibling() == -1},
			removeFromParent(){this.$parent.removeChild(this)},
			select(){this.root.selectInstance(this)},
			deselect(){this.root.deselectInstance(this)}
		},

		shared : {

			addChild(child){
				this.$store.commit('addChildToParent', {child, parent: this.note});
			},


			removeChild(child){
				this.$store.commit('removeChildFromParent',{child: child.note, parent: this.note});
			},

			path(){
				if (this == this.root){return [this]}
				return this.$parent.path().concat([this]);
			},

			resizeAll(){
				setTimeout(this.resize,50); //TODO hacky
				
				this.$children.forEach(function(child){child.resizeAll();})
			},

			onClick(e){
			this.$emit('click',{instance: this, event: e});
			},
		}
		
	}
}