import Vue from 'vue'
import Vuex from 'vuex'
import helpers from './helperfns.js'

Vue.use(Vuex)

export class Note {
	constructor(id){
		this.id = id;
		this.content = '';
		this.description = "";
		this.children = [];
		this.autoExpand = false;
	}
}

export default new Vuex.Store({
  state: {
		notes : {
			0 : new Note(0)
		},

		CONSTANTS : {
			SPELLCHECK : 'false',
			TAB_WIDTH: 4,
		}

		


	},

	getters: {
		getContent: (state) => (id) => {
			return state.notes[id].content;
		},
		
		getDescription: (state) => (id) => {
			return state.notes[id].description;
		},

		getNotes(state){
			return state.notes;
		},

		noteHasChild: (state) => (note,child) => {
			return note.children.indexOf(child.id) != -1
		},

		getNoteById: (state) => (id)=>state.notes[id],

		/*IDs (and state.notes.length)s are strictly increasing during a use of this app. Removal of orphaned notes is done during DL, while their remapping to ints is done during UL.*/
		newId: (state) => Object.keys(state.notes).length 

		
	},

	mutations: {

		registerNewNote(state, id){
			var newNote = new Note(id);
			state.notes = {...state.notes, [id]:newNote};

		},

		addNote(state, note){
			state.notes = {...state.notes, note};
		},

		addChildToParent(state, {child, parent}){
			parent.children.push(child.id);
		},

		removeChildFromParent(state,{child, parent}){ 
				parent.children = parent.children.filter(candidate => candidate!=child.id);
		},

		setContent(state,{id, content}){
			state.notes[id].content = content;
		},

		setDescription(state,{id, description}){
			state.notes[id].description = description;
		},

		//Assumes there is a root, new_notes[0], which the rest fall under
		loadNotes(state,{new_notes,fileName}){
			var cur_root = state.notes[0];
			var new_root = new_notes[0];
			
			state.notes = helpers.mergeNotes(state.notes,new_notes);
			cur_root.children.push(new_root.id);
			new_root.content=fileName;
		},

		//Assumes child can be shifted down (i.e. child in parent and not last child)
		shiftDown(state,{parent,child}){
			var c = parent.children;
			var i = c.indexOf(child.id);
			[c[i],c[i+1]]=[c[i+1],c[i]];
		},

		//Assumes child can be shifted up (i.e. child in parent and not first child)
		shiftUp(state,{parent,child}){
			var c = parent.children;
			var i = c.indexOf(child.id);
			[c[i],c[i-1]]=[c[i-1],c[i]];
		},

	},

	actions: {
	
	}
})
