<template>
	<div class="base">
		<div :class="{elements:true, selected: isSelected, editable: iseditable, focused: isFocused, fihsn: focusedInstanceHasSameNote}" @click.stop="onClick" @dblclick.stop="onDblclick" v-bind:style="{height:height+'px'}">
			<button @click="toggleChildren" :class="{boldButton : children.length>0}"> {{button_text}} </button>
			<textarea type="text" :spellcheck="SPELLCHECK" :readonly="!iseditable" v-model:value="text"/> </textarea>
		</div>

		<div v-if="children_instantiated" :class="{hidden : button_text=='>' }" >
			<NoteInstance v-for="child in children" :note_id="child"/>
		</div> 

	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'
import instance from './instance.js'

export default {

	name: 'NoteInstance',
	
	components: {
	},

	props:{
		note_id: Number
	},

	data(){
		
		return {
			button_text: '>',

			children_instantiated : false,

			is_root : false,

			height : 10
		}
	},

	methods: {
		...instance.methods.NoteInstanceOnly,
		...instance.methods.shared
	},
 
	computed: {
		...mapState({
			children(state){
				var note = state.notes[this.note_id];
				if (note == undefined){debugger;}
				var children = note.children;
				return children;
			},

			parents(state){
				return state.notes[this.note_id].parents;
			},

			parentNote(state){
				return this.$parent.note;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		//show_children(){return this.children_instantiated && this.button_text=='v'},

		primaryDiv(){return this.$el.children[0]},

		textArea(){return this.$el.children[0].children[1]},

		siblings(){return this.$parent.$children},

		index(){return this.indexAsSibling()},

		isFolded(){
			return this.button_text == '>';
		},

		focusedInstanceHasSameNote(){
			return (this.root.focusedInstanceNote==this.note && this.root.focusedInstance!=this)
		},

		editableElement: {
			set(element){
				this.root.editableElement = element;
			},
			get(){
				return this.root.editableElement;
			}
		},

		text : {
			set(text){
				if (this.root.showDescriptions){
					this.description = text;
				}
				else {
					this.content = text;
				}
			},
			get(){
				if (this.root.showDescriptions){
					return this.description;
				}
				else {
					return this.content;
				}

			}
		},

		description: {
			set(description){
				this.$store.commit('setDescription', {id: this.note_id, description});
				if(this.height != this.textArea.scrollHeight){this.resize()};
			},
			get(){
				this.$nextTick(this.resize.bind(this));
				return this.$store.getters.getDescription(this.note_id);

			}
		},
		
		content: {
			set(content){
				this.$store.commit('setContent', {id: this.note_id, content});
				if(this.height != this.textArea.scrollHeight){this.resize()};
			},
			get(){
				this.$nextTick(this.resize.bind(this));
				return this.$store.getters.getContent(this.note_id);

			}
		},

		root(){
			if (this.is_root){return this;}
			return this.$parent.root;
		},

		isSelected(){
			var root = this.root; 
			var isSel = root.instanceIsSelected(this);
			
			return isSel;
		},

		iseditable(){
			return this.root.editableInstance == this;
		},

		isFocused(){
			return this.root.focusedInstance == this;
		},

		isExpanded() { return this.button_text == 'v'},

		SPELLCHECK() { return this.$store.state.CONSTANTS.SPELLCHECK;}
	},

	mounted(){

		this.$on('click',function({instance, event}){
			this.$parent.$emit('click',{instance, event});
		});

		this.$on('dblclick',function({instance, event}){
			this.$parent.$emit('dblclick',{instance, event});
		});

		if(this.note.autoExpand){this.toggleChildren()}
	}
};

</script>
<style scoped>

	textarea {
		height: auto;
		overflow-y: auto;
		width: 100%;
		padding: 0;
		overflow-y: hidden;
		padding-left:3px;
		padding-top: 1px;
		padding-bottom: 3px; /*larger than top padding to account for 'g'*/

		font-family: sans-serif;

		-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
	    -moz-box-sizing: border-box;    /* Firefox, other Gecko */
	    box-sizing: border-box;    

	}

	button,textarea {
		border: 1px solid black;
	}

	button {
		border-right: none;
	}

	.boldButton {
		font-weight: bold;
	}

	.base {
		float: right;
		width: 98%;
		display: block;
		padding-top: 2px;
	}	

	.elements{
		min-height: 1em;
		width: 100%;
		display: flex;	
	}

	.selected > textarea{ 
	    border-color: pink;
	    border-width: 2px;
	}

	.focused > textarea{ 
	    border-color: red;
	    border-width: 2px;
	}


	.hidden{
		display: none;
	}

	.editable {

	}

	.fihsn > textarea{
		border-color: green;
		border-width: 2px;
	}

</style>