<template>
	<div :class="{NoteContainer:true, selected:isSelected, focused: isFocused}" @click.stop="onClick" style="padding: 2px">
		<NoteInstance class="instance" v-for="child in children" :note_id="child"/>
	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import NoteInstance from './NoteInstance.vue'
import instance from './instance.js'
import helperfns from '../helperfns.js'

export default {

	name: 'NoteContainer',
	
	components: {
		NoteInstance
	},

	data(){
		
		return {
			id: '',
			note_id: '0',
			_editableInstance: null, //typing changes its text.
			_selectedInstances : [],//noteInstance functions act on these when user invokes
			_focusedInstance: this,//instance with cursor
			root: this,
			is_root: true,
			clipboard : {
				notes : []
			},
			editableElement: null,
			showDescriptions: false
		}
	},

	methods: {

		...instance.methods.shared,

		callFn(fn,forAllSelectedInstances=true,isDoableOnRoot=false,callBackFn=function(){}){
			fn = fn.bind(this);
			var instances = forAllSelectedInstances ? this.selectedInstances.filter(()=>(true)) : [this.selectedInstances[0]];
			
			var cycle = function(){
				if (instances.length==0){ callBackFn(); return; }
				var instance = instances.pop();
				if (isDoableOnRoot || !instance.is_root){fn(instance);}
				this.$nextTick(cycle);
			}.bind(this);
			cycle();
		},


		selectThis(){
			this.selectInstance(this);
		},	

		selectInstance(instance, appendMode=true){
			if (!appendMode){
				this.selectedInstances = [instance];
				return;
			}

			if(this.selectedInstances.includes(instance)){return;}
			this.selectedInstances = [...this.selectedInstances, instance]; 
		
		},

		deselectInstance(instance){
			this.selectedInstances = this.selectedInstances.filter( (i) => (i!=instance) );
		},

		instanceIsSelected(instance){ 	
			return this.selectedInstances.includes(instance)
		},

		focus(instance, appendMode=false){
			this.focusedInstance = instance;
			this.selectInstance(instance, appendMode);
		},

		//Sel instance furtherst up vertically in view (not necc at highest level)
		highestSelectedInstance(){
			var cur = this;
			do{
				if(cur.isSelected){return cur}
				cur = this.nextVisibleInstance(cur)
			} 
			while(cur != this)
			debugger;
		},


		getNewNote(){
			var id = this.newId;
			this.$store.commit('registerNewNote', id);
			var newNote = this.$store.getters.getNoteById(id);
			return newNote;
		},	

		//next instance below <instance> on the screen.
		nextVisibleInstance(instance){
				if(instance.$children.length>0){return instance.$children[0]}
				if(!instance.isLastSibling()){return instance.nextSibling()}
				var parent = instance.$parent;
				while (parent!=instance.root){
					if (!parent.isLastSibling()){return parent.nextSibling()}
					else 						{parent=parent.$parent}
				}
				return parent;
		},

		previousVisibleInstance(instance){
			var i = this;
			var next = this.nextVisibleInstance(i);
			while (next!=instance && next!=this){
				i = next; 
				next = this.nextVisibleInstance(i);
			}
			return i;
		},
	
		addToClipboard(instance){
			this.clipboard.notes.push(instance.note);
		},

		attachAllNotesOnClipboard(instance){
			this.clipboard.notes.forEach(function(note){
				this.$store.commit('addChildToParent',{child: note, parent:instance.note});
				instance.expand();
			}.bind(this));
		},

		clearEditable(){ 
			if(this.$data._editableInstance!=null){
				var ta = this.$data._editableInstance.textArea;
				ta.selectionStart = ta.selectionEnd = 0;
			}
			this.$data._editableInstance = null;
		},

		lowestVisibleElement(path){
			var lvi=0; //last visible element's index. 0 is root, so is always visible.
			while(path.length>lvi+1 && path[lvi+1].isVisible()){
				lvi++;
			}
			return path[lvi];
		},

		//Excludes root
		getOrderedSelectedInstances(){
			var ordered_selected_instances = [];
			var cur = this.nextVisibleInstance(this); 
			while (cur != this){
				if (this.instanceIsSelected(cur)){ordered_selected_instances.push(cur)}
				cur = this.nextVisibleInstance(cur);
			}
			return ordered_selected_instances;
		},


		//Many functions that act on instances also act on the container. These fns are simple counterparts of noteinstance fns. 
		expand(){}, 
		isVisible(){return true},
		wasDeleted(){return false},
		toggleChildren(){},
		scrollIntoView(){},
		shiftUp(){},
		shiftDown(){},
		isLastSibling(){return true},
		isFirstSibling(){return true},
		resize(){},
		nextSiblingNoWrap(){return this}



	},
 
	computed: {
		...mapState({
			children(state){
				var children = state.notes[this.note_id].children;
				return children;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		...mapGetters(['newId']),


		isSelected(){ return this.instanceIsSelected(this)},

		isFocused(){ return this.focusedInstance == this },

		firstSelectedInstance(){ return this.selectedInstances[0] },

		focusedInstanceNote(){
			return this.focusedInstance.note
		},

		selectedInstances : {
			set(instances){
				this.clearEditable();
				this.$data._selectedInstances = instances;

				
			},
			get(){
				return this.$data._selectedInstances;
			}
		},

		editableInstance : {
			set(instance){
				if (!instance){exception()} //Use clearEditable instead

				this.selectInstance(instance, false);
				//if (this.$data._editableInstance) {this.$data._editableInstance.textArea.blur();}
				this.$data._editableInstance = instance;

			
				this.$nextTick(function(){
					var ta = this.$data._editableInstance.textArea;
					ta.focus();
					ta.selectionEnd = ta.selectionStart;
				}.bind(this));	
				
				
			},
			get(){
				return this.$data._editableInstance;
			}
		},

		focusedInstance: {
			set(instance){
				this.$data._focusedInstance = instance;
			},

			get(){
				return this.$data._focusedInstance;
			}
		}

		

	},

	created(){
		document.addEventListener('keydown', e => {
			if (this.selectedInstances.length == 0){return;}
			if (this.editableInstance != null && (e.key!='Alt' && e.key!='Tab' && e.key!='Insert')){return;}
			if (e.code=="Space" && e.ctrlKey){this.showDescriptions = !this.showDescriptions; this.resizeAll();}
			switch(e.key){
				case 'Alt':
					e.preventDefault();
					if (this.editableInstance != null){this.clearEditable(); }
					break;

				case 'Insert':
					//Design deicision: clicking insert with multiple instances selected creates ONE new note and adds it to all selected.
					
					var newNote = this.getNewNote();
					var newSelectedInstances = [];
					var alt = e.altKey;


					this.callFn(
						function(instance){
							instance.addChild(newNote);
							instance.expand();
							this.$nextTick(function(){
								newSelectedInstances.push(instance.$children[instance.$children.length-1]);
							})
						},
						//forAllSelectedInstances
						true, 
						//isDoableOnRoot 
						true,  
						//callBackFn 
						function(){ 
							if (alt) return;
							this.focus(newSelectedInstances[0]);
							this.scrollIntoView(newSelectedInstances[0]);
							this.selectedInstances = newSelectedInstances;
						}.bind(this)
					);
					break;

				case 'Delete': 
					var path = this.selectedInstances[0].path();
					this.callFn(
						function(instance){instance.removeFromParent()},
						true,
						false,
						function(){this.focus(this.lowestVisibleElement(path))}.bind(this)
					);
					break; 

				//Copy selected instances to local clipboard and clear OS clipboard
				case 'c':
					if (!e.ctrlKey){break}

					this.clipboard.notes = [];
					this.callFn(this.addToClipboard);

					//Clear the OS clipboard
					// navigator.permissions.query({name: "clipboard-write"}).then(function(result){
					//   if (result.state == "granted" || result.state == "prompt") {
					//   	navigator.clipboard.writeText(JSON.stringify(this.clipboard));
					//   }
					// }.bind(this));

					break;

				//If ctrl-v is pressed and the OS clipboard is empty, then copy notes of local clipboard under selected instances
				case 'v': 
					if (!e.ctrlKey){break}
					this.callFn(this.attachAllNotesOnClipboard,true,true) 
					// navigator.clipboard.readText().then(text => {});
					break;

				case 'Enter':
					e.preventDefault();	
					if (this.focusedInstance != this){
						this.editableInstance = this.focusedInstance;
					}
					break;

				case 'ArrowUp':
					e.preventDefault();
				
					var fi = this.focusedInstance;

					if (!e.shiftKey){
						var fi = this.focusedInstance;
						var nvi = this.previousVisibleInstance(fi);
						this.focus(nvi,e.ctrlKey)
					}

					if (e.shiftKey){
						var selectedInstances = this.getOrderedSelectedInstances().reverse();
						while(selectedInstances.length>0){
							var inst = selectedInstances.pop();
							if (!inst.isFirstSibling()){
								var ps =inst.previousSibling();
								if (this.instanceIsSelected(ps)){continue}
								inst.shiftUp();

								inst.deselect();
								ps.select();
								
							}
						}
					this.focus(this.highestSelectedInstance(),true);
					}


					break;

				case 'ArrowDown':
					e.preventDefault();

					if (!e.shiftKey){
						var fi = this.focusedInstance;
						var nvi = this.nextVisibleInstance(fi);
						this.focus(nvi,e.ctrlKey)
					}

					if (e.shiftKey){
						var selectedInstances = this.getOrderedSelectedInstances();
						while(selectedInstances.length>0){
							var inst = selectedInstances.pop();
							if (!inst.isLastSibling()){
								var ns =inst.nextSibling();
								if (this.instanceIsSelected(ns)){continue}
								inst.shiftDown();

								inst.deselect();
								ns.select();

							}
						}
					this.focus(this.highestSelectedInstance(),true);

					}

					
					break;

				case 'ArrowLeft':
					if(this.editableInstance!=null){break;}

					e.preventDefault();
					if(this!=this.focusedInstance){
						this.focus(this.focusedInstance.$parent,e.ctrlKey);
					}
					break;

				case 'ArrowRight':
					if(this.editableInstance!=null){break;}

					e.preventDefault();
					var fi = this.focusedInstance;
					if (fi.$children.length > 0){this.focus(fi.$children[0],e.ctrlKey)}
					break;


				case 'Tab':
					e.preventDefault();

					if (this.editableInstance){
						//Tabbing logic. Assumes nothing is highlighted.	
						var ta = this.editableInstance.textArea;
						var text = ta.value;
						var index = ta.selectionStart;
						var tab_width = this.$store.state.CONSTANTS.TAB_WIDTH;
						var spaces_to_add = tab_width - index%tab_width;
						ta.value = text.substring(0,index) + " ".repeat(spaces_to_add) + text.substring(index);
						ta.selectionStart = ta.selectionEnd = index + spaces_to_add;
					}

					else{
						this.selectedInstances.forEach(function(inst){inst.toggleChildren();});
					}
						
					break;	

			}
		});	
	},

	mounted(){
		this.$on('click',function({instance, event}){
				this.focus(instance, event.ctrlKey);
		});

		this.$on('dblclick',function({instance, event}){
			this.editableInstance = instance;
		});

		document.title = "TNotepad";
	}
};

</script>

<style scoped>
	
	.NoteContainer {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%; 
		background-color: grey;
		margin: 3px;
		overflow: auto;
	}

	.instance {
		display: block;
		width: 100%;
		padding: 1px;
	}

	.selected{ 
	    background-color: #8C8888;
	    box-shadow: 0 0 10px orange;
	}

	.focused {
		box-shadow: 0 0 10px red;
		text-align: center;
	}


</style>